let bannerAnimate = document.querySelector('.events__banner-container--support')

let tl = new TimelineMax({
    repeat: -1
})

tl.to(bannerAnimate, 1, {
    left: '55%',
    top: '11%'
})
tl.to(bannerAnimate, 1, {
    left: '80px',
    top: '33%'
})
tl.to(bannerAnimate, 1, {
    left: '55%',
    top: '11%'
})
tl.to(bannerAnimate, 1, {
    left: '0px',
    top: '0px'
})

tl.play().then(() => {
    tl.reverse()
})

let aida = document.querySelectorAll('.events__premiere-animate')

console.log(aida);


let tl2 = new TimelineMax({
    repeat: -1
})

tl2.fromTo(aida, 0.6, {
    stagger: 0.3,
    opacity: 1
}, {
    stagger: 0.3,
    opacity: 0
})

tl2.play().then(() => {
    tl2.reverse()
})

