let head = document.querySelector('.head')

console.log(head);
let index = -10000000

head.addEventListener('mousemove', (e) => {
    console.log(index);
    index++
    console.log(index);
    head.insertAdjacentHTML('afterbegin', `<img src="./img/bg-head.png" style="position:absolute; top: ${e.clientY}px; left: ${e.clientX}px; z-index: ${index++};">`)
})